/** Import all the reference constant after this line */
@use "../../../Constant/font.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */

.counterV2_container {
    border: 1px solid $not_activated;
    box-sizing: border-box;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
}

.counterV2_inputContainer {
    outline: none;
    color: $gray_2;
    font: $fs_r_14;
    border: 0;
    margin: 0;
    padding: 0;
    width: calc(100% - 3rem);
    text-align: center;
    display: inline-block;
}

.counterV2_inputContainer__disabled {
    color: $line_light;
    background: transparent;
    pointer-events: none;
}

.counterV2_subtractBtnIcon {
    font-size: 0.1rem;
}

.counterV2_addBtn,
.counterV2_subtractBtn {
    width: 1.3rem;
    height: 1.3rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
    &__disabled {
        cursor: default;
        > svg {
            color: $line_light;
        }
    }
    > svg {
        font-size: 1.6rem;
    }
}

.counterV2_addBtnIcon {
    font-size: 1.3rem;
}

.counterV2_subtractBtnIcon,
.counterV2_addBtnIcon {
    color: $primary_variant;
}

.counterV2_subtractBtn__gray,
.counterV2_addBtn__gray {
    cursor: context-menu;
    .counterV2_subtractBtnIcon,
    .counterV2_addBtnIcon {
        color: $line_light;
    }
}
